"use client";

import React from "react";
import MultiProgress from "react-multi-progress";

interface StepperProps {
  currentStep: number;
  totalSteps?: number;
  title?: string;
}

/**
 * Workly Stepper (Horizontal Equal Segments)
 * Uses react-multi-progress safely with TypeScript and global color variables.
 */
export default function WorklyStepper({
  currentStep,
  totalSteps = 6,
  title,
}: StepperProps) {
  // âœ… Create equal segments using global color tokens
  const segments = Array.from({ length: totalSteps }, (_, i) => ({
    value: 100 / totalSteps,
    color:
      i < currentStep
        ? "var(--color-btn-primary-500)" // active segment
        : "var(--color-btn-teritary-200)", // inactive segment
  }));

  return (
    <div className="flex flex-col mb-4">
      {/* Step title */}
      {title && (
        <h2 className="text-base font-semibold text-[var(--color-text-primary)] mb-2">
          Step {currentStep} : {title}
        </h2>
      )}

      {/* Progress bar */}
      <div className="flex items-center justify-between">
        <div className="flex-1">
          <MultiProgress
            height={8}
            elements={segments}
            background="var(--color-btn-teritary-200)"
            transitionTime={0.3}
            {...({ borderRadius: 50 } as any)}
          />
        </div>

        <span className="text-sm text-[var(--color-text-secondary)] ml-2">
          {currentStep}/{totalSteps}
        </span>
      </div>

      {/* Optional input example for placeholder styling */}
      <input
        type="text"
        placeholder="Example placeholder text"
        className="mt-3 text-sm px-3 py-2 border border-[var(--color-btn-teritary-200)] 
                   rounded-lg text-[var(--color-text-primary)] 
                   placeholder-[var(--color-text-muted)] 
                   bg-[var(--color-bg-neutral-50)] focus:ring-2 
                   focus:ring-[var(--color-primary)] focus:outline-none"
      />
    </div>
  );
}
